<template>
    <div class="massage">
        <div class="header">
            <p>留言</p>
        </div>
        <Pepolemessage :comments="comments" />
    </div>
</template>

<script>
    import Pepolemessage from "./Pepolemessage";
    import { getMassageData } from "../../../../service/api";

    export default {
        name: "Messageboard",
        data() {
            return {
                comments: []
            };
        },
        components: {
            Pepolemessage
        },
        created() {
            //请求网络数据
            getMassageData()
                .then(response => {
                    this.comments = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
        },
        methods: {
            senMassage() {
                getMassageData()
                    .then(response => {
                        this.comments = response.data;
                        this.comments.splice(0, 0);
                    })
                    .catch(error => {
                        console.log(error);
                    });
            }
        }
    };
</script>

<style lang="scss" scoped>
    .header {
        width: 100%;
        height: 38px;
        font-size: 20px;
        line-height: 38px;
        p {
            border-bottom: 2px solid #60b2ec;
        }
    }

    .massage {
        width: 100%;
        padding: 25px 20px;
        border-radius: 10px;
        box-sizing: border-box;
        position: relative;
    }
</style>